#appContainer{
  max-width: 500px;
  height: 100%;
  max-height: 650px;
  min-width: 360px;
  position: relative;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  background: #fff;
  /* display: inline-block;*/
  }
  #mainContent{
    width: 100%;
    overflow-y: auto;
    height: calc(100% - 170px);
  
  }
  #mainContent:after{
    
    content: "";
    /*background-image: url(data:image/svg+xml;base64,);*/
    opacity: 0.1;
    width: 100%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    height: calc(98vh - 80px);
    position: absolute;
    z-index: -1;
  
  }

  #chat{
    height: inherit;
  }